/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import styled from "styled-components";

const EmployeeTable1 = ({ data }) => {
  const ex = new Date(data.expire);
  const nyear = new Date();
  var xyr = ex.getFullYear();
  var xmo = ex.getMonth();
  var nyr = nyear.getFullYear();
  var nmo = nyear.getMonth();

  const [month, setMonth] = useState(xmo);
  const [year, setYear] = useState(xyr);

  useEffect(() => {
    if (xmo < nmo) {
      setYear(year - 1);
      setMonth(xmo + 12 - nmo);
    } else {
      setMonth(month - nmo);
    }
    setMonth((year - nyr) * 12 + month);
  }, []);

  return (
    <TRow>
      <TData>
        <Image src={data.image} alt="avatar" />
      </TData>
      <TData>{data.name}</TData>
      <TData>{data.passport}</TData>
      <TData>{data.country}</TData>
      <TData>{data.date}</TData>
      <TData month={month} style={month <= 3 ? { color: "red" } : null}>
        {data.expire}
      </TData>
      <TData>{data.lastupdate}</TData>
    </TRow>
  );
};

export default EmployeeTable1;

export const Table = styled.table({
  width: "100%",
  marginBottom: "10px",
});

export const THead = styled.thead({});

export const TBody = styled.tbody({});

export const TRow = styled.tr({
  padding: "0.3rem 0",
  background: "rgba(241, 242, 246,0.2)",
  border: "1px solid #ced4da",
  width: "100%",
});

export const TData = styled.td((props) => ({
  fontWeight: 400,
  fontSize: "1rem",
  padding: "0.5rem 1rem",
  whiteSpace: "wrap",
  position: "relative",

  ":nth-child(6)": {
    color: "#333 !important",
    fontWeight: 400,
    "::before": {
      position: "absolute",
      content: "''",
      width: "2%",
      height: "30px",
      display: "block",
      top: "0",
      left: "0",
      bottom: "0",
      margin: "auto",
      color: "#333",
      background:
        props.month > 3
          ? "rgba(46, 213, 115, 1)"
          : props.month > 1
          ? "rgba(255, 165, 2,1)"
          : "rgba(234, 32, 39, 1)",
      borderRadius: "5px",
      "@media (min-width: 1200px)": {
        width: "2%",
      },
      "@media (max-width: 992px)": {
        width: "6%",
      },
    },
  },

  ":nth-child(1)": {
    width: "62px",
  },
  "@media (max-width: 768px)": {
    ":nth-child(7)": {
      display: "none",
    },
  },
  "@media (max-width: 687px)": {
    ":nth-child(4)": {
      display: "none",
    },
  },
  "@media (max-width: 550px)": {
    fontSize: "0.9rem",
    ":nth-child(1)": {
      width: "62px",
    },
  },
  "@media (max-width: 450px)": {
    fontSize: "0.7rem",
    padding: "0.5rem 0.35rem",
  },
}));

export const Image = styled.img({
  width: "30px",
  height: "auto",
  borderRadius: "5px",
  border: "2px solid rgba(255, 165, 2,1.0)",
});
