/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState, lazy } from "react";
import styled from "styled-components";
import addNotification from "react-push-notification";
import emp from "../components/Image/employee.png";
import safe from "../components/Image/shield.png";
import danger from "../components/Image/warning.png";
import reject from "../components/Image/rejected.png";
// import { Calendar } from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { EmpInformation } from "../data/EmpInfo";
import EmployeeTable1, {
  TBody,
  TRow,
} from "../components/tables/EmployeeTable1";
import { useLocation, useNavigate } from "react-router-dom";
import secureLocalStorage from "react-secure-storage";
import { useDispatch } from "react-redux";
import { setLogin } from "../feature/UserSlice";
const LoadingPage = lazy(() => import("../components/LoadingPage"));

const Home = () => {
  // const [date, setDate] = useState(new Date());
  const [safeZone, setSafeZone] = useState(0);
  const [dangerZone, setDangerZone] = useState(0);
  const [rejectedZone, setRejectedZone] = useState(0);
  const [loading, setLoading] = useState(true);
  const [total, setTotal] = useState(0);
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useDispatch();

  useEffect(() => {
    const data = secureLocalStorage.getItem("data");
    if (data !== null) {
      EmpInformation.map((dt) => {
        if (dt.username === data.username && dt.password === data.password) {
          dispatch(setLogin(data));
        }
        return null;
      });
    } else {
      navigate("/");
    }

    setTotal(EmpInformation.length);
    const sZone = EmpInformation.filter((data) => {
      const ex = new Date(data.expire);
      const nyear = new Date();
      var xyr = ex.getFullYear();
      var xmo = ex.getMonth();
      var nyr = nyear.getFullYear();
      var nmo = nyear.getMonth();
      var y = xyr;
      var m = xmo;
      if (xmo < nmo) {
        y = y - 1;
        m = xmo + 12 - nmo;
      } else {
        m = m - nmo;
      }
      m = (y - nyr) * 12 + m;
      if (m > 3) {
        return data;
      }

      return null;
    });
    const dZone = EmpInformation.filter((data) => {
      const ex = new Date(data.expire);
      const nyear = new Date();
      var xyr = ex.getFullYear();
      var xmo = ex.getMonth();
      var nyr = nyear.getFullYear();
      var nmo = nyear.getMonth();
      var y = xyr;
      var m = xmo;
      if (xmo < nmo) {
        y = y - 1;
        m = xmo + 12 - nmo;
      } else {
        m = m - nmo;
      }
      m = (y - nyr) * 12 + m;
      if (m <= 3 && m >= 0) {
        return data;
      }
      return null;
    });
    setSafeZone(sZone.length);
    setDangerZone(dZone.length);
    setRejectedZone(EmpInformation.length - (sZone.length + dZone.length));
    setLoading(false);
    if (dZone.length > 0) {
      getNotification(dZone.length);
    }
  }, [dispatch, navigate]);

  const getNotification = (val) => {
    addNotification({
      title: `Visa Expiration Problem.`,
      subtitle: "Hello",
      message: `${val} Employee's are in  danger zone. Please solve the problem.`,
      theme: "darkblue",
      duration: 8000,
      native: true,
      onClick: () => {},
      backgroundTop: "green",
      backgroundBottom: "darkgreen",
      colorTop: "green",
      colorBottom: "darkgreen",
      closeButton: "Go away",
    });
  };

  return (
    <React.Fragment>
      {loading ? (
        <LoadingPage />
      ) : (
        <Section>
          <HeaderTitle>Dashboard</HeaderTitle>
          <CardContainer>
            <Card>
              <CardImage src={emp} alt="employee" />
              <CardBody>
                <h5>Total Employee</h5>
                <h2>{total}</h2>
              </CardBody>
            </Card>
            <Card>
              <CardImage src={safe} alt="employee" />
              <CardBody>
                <h5>Safe Zone</h5>
                <h2>{safeZone}</h2>
              </CardBody>
            </Card>
            <Card>
              <CardImage src={danger} alt="employee" />
              <CardBody>
                <h5>Dander Zone</h5>
                <h2>{dangerZone}</h2>
              </CardBody>
            </Card>
            <Card>
              <CardImage src={reject} alt="employee" />
              <CardBody>
                <h5>Rejected</h5>
                <h2>{rejectedZone}</h2>
              </CardBody>
            </Card>
          </CardContainer>
          <EmployeeContainer>
            <EmployeeInnerContainer>
              <HeaderTitle className="headertitle">
                Employee Information
              </HeaderTitle>
              <Table>
                <thead className="table-head">
                  <TRow>
                    <THead>Img</THead>
                    <THead>Name</THead>
                    <THead>Passport</THead>
                    <THead>Country</THead>
                    <THead>Issue</THead>
                    <THead>Expiry</THead>
                    <THead>Last Update</THead>
                  </TRow>
                </thead>
                <TBody>
                  {EmpInformation.sort((a, b) => {
                    const aexpiry = new Date(a.expire);
                    const bexpiry = new Date(b.expire);
                    if (aexpiry < bexpiry) {
                      return -1;
                    } else {
                      return 1;
                    }
                  }).map((data, idx) => {
                    if (idx > 6) {
                      return null;
                    } else {
                      return <EmployeeTable1 key={data.id} data={data} />;
                    }
                  })}
                </TBody>
              </Table>
            </EmployeeInnerContainer>
            {/* <CalenderContainer>
          <h1 className="header">Calendar</h1>
          <div className="calendar-container">
            <Calendar className="calendar" onChange={setDate} value={date} />
          </div>
        </CalenderContainer> */}
          </EmployeeContainer>
          <ButtonContainer>
            <Button
              onClick={() => {
                const path = location.pathname.replace(
                  "/dashboard",
                  "/employee"
                );
                navigate(path);
              }}
            >
              More
            </Button>
          </ButtonContainer>
        </Section>
      )}
    </React.Fragment>
  );
};

export default Home;

export const Section = styled.section({
  width: "100%",
  minHeight: "90vh",
  display: "flex",
  flexDirection: "column",
  padding: "90px 1rem 1rem 1rem",
  overflow: "hidden",
  "@media (max-width: 992px)": {
    paddingTop: "55px",
  },
});

export const HeaderTitle = styled.h1({
  fontSize: "1.5rem",
  color: "rgba(3,3,3, 0.6)",
  margin: "1rem 0",
});

export const CardContainer = styled.div({
  display: "grid",
  gridTemplateColumns: "auto auto auto auto",
  gap: "0.5rem",

  "@media (max-width: 992px)": {
    gridTemplateColumns: "auto auto",
  },
  "@media (max-width: 600px)": {
    gridTemplateColumns: "auto",
  },
});

export const Card = styled.div({
  width: "100%",
  padding: "1.5rem 1rem",
  borderRadius: "5px",
  display: "flex",
  flexDirection: "row",
  flexWrap: "nowrap",
  alignItems: "center",
  border: "1px solid rgba(3,3,3, 0.1)",
  boxShadow: "1px 1px 0 0 rgba(3,3,3, 0.1)",
  "&:nth-child(1)": {
    background: "rgba(55, 66, 250,0.4)",
  },
  "&:nth-child(2)": {
    background: "rgba(46, 213, 115,0.4)",
  },
  "&:nth-child(3)": {
    background: "rgba(255, 71, 87,0.4)",
  },
  "&:nth-child(4)": {
    background: "rgba(255, 165, 2,0.4)",
  },
});

export const CardImage = styled.img({
  width: "50px",
  height: "50px",
  marginRight: "10px",
  opacity: "0.5",
});

export const CardBody = styled.div({
  display: "flex",
  flexDirection: "column",
});

export const EmployeeContainer = styled.div({
  // display: "flex",
  // flexDirection: "row",
  // flexWrap: "wrap",
  marginTop: "2rem",
  display: "grid",
  gridTemplateColumns: "auto",
  gap: "1rem",
  "@media (max-width: 992px)": {
    gridTemplateColumns: "auto",
  },
});
export const CalenderContainer = styled.div({
  display: "flex",
  flexDirection: "column",
});
export const EmployeeInnerContainer = styled.div({});

export const Table = styled.table({
  width: "100%",
  tableLayout: "fixed",
});
export const THead = styled.th({
  fontWeight: 600,
  fontSize: "1rem",
  padding: "0.5rem 1rem",
  whiteSpace: "wrap",
  textAlign: "left",
  ":nth-child(1)": {
    width: "62px",
  },
  "@media (max-width: 768px)": {
    ":nth-child(7)": {
      display: "none",
    },
  },
  "@media (max-width: 687px)": {
    ":nth-child(4)": {
      display: "none",
    },
  },
  "@media (max-width: 550px)": {
    fontSize: "0.9rem",
    ":nth-child(1)": {
      width: "52px",
    },
  },
  "@media (max-width: 450px)": {
    fontSize: "0.7rem",
    padding: "0.5rem 0.35rem",
  },
});

export const ButtonContainer = styled.div({
  width: "100%",
  padding: "1rem",
  display: "flex",
  alignItems: "center",
  justifyContent: "flex-end",
});

export const Button = styled.button({
  padding: "0.5rem 3rem",
  border: "none",
  outline: "none",
  fontWeight: "600",
  color: "#fff",
  background: "rgba(255, 71, 87,1.0)",
  cursor: "pointer",
});
